@import '../../sass/utils/mixin';

.sectionServiceList {
    @include mq(desk) {
        padding-top: 50px;
        padding-bottom: 50px;
    }
}

.serviceList__title {
    @include mq(desk) {
        font-family: Lato;
        font-size: 26px;
        line-height: 1.3;
        text-align: center;
        margin-bottom: 30px;
    }
}

.serviceList {
    @include mq(desk) {
        display: grid;
        gap: 1rem;
        grid-template-columns: repeat(4, 1fr);
        justify-content: center;
        justify-items: center;
        width: 1080px;
        margin: 0 auto;
    }
}

.serviceList__item {
    @include mq(desk) {
        width: 240px;
        height: 180px;
        padding: 10px;
        background-color: aqua;
    }
}